<template>
<div class="job-list">
	<div class="page-header">
		<mt-header class="container" :title="manager.SearchJobNumber + ' / ' + manager.SearchDateTime">
			<span slot="left" @click="onShowMenu">
				<font-awesome-icon icon="bars" size="2x" />
			</span>
			<span slot="right" @click="onAddNew">
				<font-awesome-icon icon="plus" size="2x" />
			</span>
		</mt-header>
	</div>

	<div class="page-body container">
		<div v-for="(item, index) in listArray" :key="item.HRTimeSheetPK" class="wrapper">
			<mt-cell>
				<span slot="title" @click="onDetail(item.HRTimeSheetPK)">
					<span class="dateTime">{{item.FirstName + ' ' + item.LastName}}</span>
					<br />
					<span class="postInOut"><font-awesome-icon icon="calendar" />{{item.PostIn + ' - ' + item.PostOut}}</span>
					<br />
					<span class="checkInOut"><font-awesome-icon icon="clock" />{{item.CheckIn + ' - ' + item.CheckOut}}</span>
				</span>

				<span class="functions" @click="onCheckInOut(index)"><font-awesome-icon icon="clock" size="2x" /></span>
				<span class="functions" @click="OnBreak(index)"><font-awesome-icon :icon="getBreakIcon(index)" size="2x" /></span>
				<span class="functions" @click="onStatus(index)"><font-awesome-icon :icon="getStatusIcon(index)" size="2x" /></span>
			</mt-cell>
		</div>

		<mt-actionsheet
			:actions="command.actions"
			v-model="command.visible"
			cancelText = "Cancel">
		</mt-actionsheet>

		<mt-popup class="menu" v-model="popupMenu.visible" position="left">
			<ul>
				<li v-if="isRegionManager" @click="onRegion">
					<font-awesome-icon icon="city" />
					<span>Region</span>
				</li>
				<li v-if="isRegionManager" @click="onEquipmentRepair">
					<font-awesome-icon icon="hammer" />
					<span>Repair</span>
				</li>
				<li @click="onLogout">
					<font-awesome-icon icon="sign-out-alt" />
					<span>Louout</span>
				</li>
			</ul>
		</mt-popup>

		<mt-popup class="search" ref="search" v-model="popupSearch.visible">
			<mt-field label="JobNumber" type="text" placeholder="please enter here" v-model="popupSearch.jobNumber"></mt-field>
			<mt-field label="Date" type="text" readonly="readonly" @click.native="onShowDatePicker" v-model="popupSearch.date"></mt-field>
						
			<div class="button-group">
			<mt-button size="large" type="primary" @click="onSearch"><font-awesome-icon icon="search"></font-awesome-icon></mt-button>
			<mt-button size="large" type="default" @click="onSearchCancel"><font-awesome-icon icon="times"></font-awesome-icon></mt-button>		
			</div>
		</mt-popup>

		<mt-datetime-picker type="date" ref="picker" v-model="popupSearch.pickerDate"
			cancelText = "Cancel"
			confirmText = "OK"
			:startDate = "startDate"
			:endDate = "endDate"
			@confirm="onPickerConfirm"
			@visible-change="onPickerVisibleChange">
		</mt-datetime-picker>

	</div>

	<div class="tabbar-wrapper">
	<mt-tabbar class="container">
		<mt-tab-item> 
			<span @click="onShowSearch">
				<font-awesome-icon icon="search" size="2x" />
			</span>
		</mt-tab-item>
	</mt-tabbar>
	</div>
</div>
</template>

<script>
import BizRCHub from '../../api/BizRCHub.js'
import Utils from '../../utils/index.js';
import HelperTimeSheet from '../../mixins/helperTimeSheet.js';

export default {
	name: 'JobTimesheetList',
	mixins: [HelperTimeSheet],
	data(){
		return {
			listArray: [],
			manager: {},

			command:{
				actions:[],
				visible: false,
				selectedIndex: 0
			},

			popupSearch:{
				jobNumber: '',
				date: '',
				pickerDate: new Date(),
				visible: false
			},

			popupMenu:{
				visible: false
			}
		}
	},
	computed:{
		startDate()
		{
			if(!this.manager)
			{
				return new Date();
			}
			return new Date(Date.parse(this.manager.MinDateTime));
		},
		endDate()
		{
			if(!this.manager)
			{
				return new Date();
			}
			return new Date(Date.parse(this.manager.MaxDateTime));
		},
		isRegionManager()
		{
			if(!this.manager)
				return false;

			return this.manager.IsCompanyManager || this.manager.IsDivisionManager || this.manager.IsRegionManager
		} 
	},
	methods:{
		getBreakIcon(index)
		{
			let status = this.getTimeSheetBreakStatus(this.listArray[index]);
			switch(status)
			{
				case 'Work':
					return 'user';
				case 'BreakOut':
					return 'coffee';
				default:
					return 'user';
			}
		},
		getStatusIcon(index)
		{
			let status = this.getTimeSheetStatusStatus(this.listArray[index]);
			switch(status)
			{
				case 'Empty':
					return 'calendar-times';
				case 'Work':
					return 'check-circle';
				case 'Sick':
					return 'hospital';
				case 'Vacation':
					return 'plane';
				case 'Absent':
					return 'question';
				case 'Terminate':
					return 'ban';
				default:
					return 'calendar-times';
			}
		},
		onShowSearch()
		{
			this.popupSearch.jobNumber = this.manager.SearchJobNumber;
			this.popupSearch.date = this.manager.SearchDateTime;
			this.popupSearch.pickerDate = new Date(Date.parse(this.manager.SearchDateTime))
			this.popupSearch.visible = true;
		},
		onShowDatePicker()
		{
			this.$refs.search.close();
			this.$refs.picker.open();
		},
		onPickerConfirm(value)
		{
			this.popupSearch.date = this.$formatDate(value, 'MM/dd/yyyy');
		},
		onPickerVisibleChange(val)
		{
			if(!val)
			{
				this.$refs.picker.close();
				this.$refs.search.open();
			}
		},
		onSearchCancel()
		{
			this.popupSearch.visible = false;
		},
		onSearch()
		{
			this.popupSearch.visible = false;
			this.getList(this.popupSearch.jobNumber, this.popupSearch.date);
		},
		getList(jobNumber, currentDate)
		{
			if(!jobNumber || !currentDate)
			{
				return;
			}

			this.$showLoading();
			BizRCHub.GetJobTimeSheetList(jobNumber, currentDate).then(res=>{
				if(res.data.OK)
				{
					this.listArray = res.data.Result;
					this.$store.commit('setJobTimeSheetList', this.listArray);
					Utils.setJobNumber(jobNumber);
					this.manager.SearchJobNumber = jobNumber;
					this.manager.SearchDateTime = currentDate;
					Utils.setUserSettings(this.manager);
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		onAddNew()
		{
			this.$router.push({name: 'JobTimesheetDetail', params: {PK: '0', JobNumber: this.manager.SearchJobNumber, DateTime: this.manager.SearchDateTime}});
		},
		onShowMenu()
		{
			this.popupMenu.visible = true;
		},
		onRegion()
		{
			this.$router.push({name: 'RegionTimesheetList'});
		},
		onEquipmentRepair()
		{
			this.$router.push({name: 'EquipmentRepairList'});
		},
		onLogout()
		{
			Utils.logout();
			this.$router.push({ name: 'Login' });
		},
		onDetail(HRTimeSheetPK) {
			this.$router.push({name: 'JobTimesheetDetail', params: {PK: HRTimeSheetPK}});
		},
		onCheckInOut(index) {
			this.scanQRClick();
			this.$takeCameraPicture((imgData)=>{
				this.$showLoading();
				let entity = this.listArray[index];
				BizRCHub.JobClock(entity.HRTimeSheetPK, imgData).then(res=>{
					if(res.data.OK)
					{
						let item = res.data.Result;
						Object.assign(entity, item);
					}
					else
					{
						this.$alert(res.data.Error, 'Error');
						console.log(res.data.Error);
					}
					this.$closeLoading();
				}).catch(err=>{
					this.showNetworkError();
					console.log(err);
					this.$closeLoading();
				});
			});
		},
		scanQRClick()
		{
			try
			{
				cordova.plugins.barcodeScanner.scan(
					result=> {
						if(!result.cancelled)
						{
							alert(result.text);
							
						}
					},
					error=> {
						alert("Scanning failed: " + error);
					},
					{
						preferFrontCamera : false, // iOS and Android
						showFlipCameraButton : true, // iOS and Android
						showTorchButton : true, // iOS and Android
						torchOn: true, // Android, launch with the torch switched on (if available)
						saveHistory: true, // Android, save scan history (default false)
						prompt : "Place a barcode inside the scan area", // Android
						resultDisplayDuration: 500, // Android, display scanned text for X ms. 0 suppresses it entirely, default 1500
						formats : "QR_CODE", // default: all but PDF_417 and RSS_EXPANDED
						orientation : "portrait", // Android only (portrait|landscape), default unset so it rotates with the device
						disableAnimations : true, // iOS
						disableSuccessBeep: false // iOS and Android
					}
				);
			}
			catch(err)
			{
				console.log(err);
			}
		},
		OnBreak(index) {
			this.$showLoading();
			let entity = this.listArray[index];
			BizRCHub.JobBreak(entity.HRTimeSheetPK).then(res=>{
				if(res.data.OK)
				{
					let item = res.data.Result;
					Object.assign(entity, item);
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		onStatus(index) {
			this.command.selectedIndex = index;

			this.command.actions = [];
			this.command.actions.push({name: 'Mark As Sick', method: this.MarkAsSick});
			this.command.actions.push({name: 'Mark As Vacation', method: this.MarkAsVacation});
			this.command.actions.push({name: 'Mark As Work', method: this.MarkAsWork});
			this.command.actions.push({name: 'Mark As Absent', method: this.MarkAsAbsent});
			this.command.actions.push({name: 'Mark As Terminate', method: this.MarkAsTerminate});

			let status = this.getTimeSheetStatusStatus(this.listArray[index]);
			switch(status)
			{
				case 'Work':
					{
						this.command.actions.splice(2, 1);
						break;
					}
				case 'Sick':
					{
						this.command.actions.splice(0, 1);
						break;
					}
				case 'Vacation':
					{
						this.command.actions.splice(1, 1);
						break;
					}
				case 'Absent':
					{
						this.command.actions.splice(3, 1);
						break;
					}
				case 'Terminate':
					{
						this.command.actions.splice(4, 1);
						break;
					}
			}
			this.command.visible = true;
		},
		MarkAsSick() {
			this.$showLoading();
			let entity = this.listArray[this.command.selectedIndex];
			BizRCHub.JobMarkAsSick(entity.HRTimeSheetPK).then(res=>{
				if(res.data.OK)
				{
					let item = res.data.Result;
					Object.assign(entity, item);
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		MarkAsVacation() {
			this.$showLoading();
			let entity = this.listArray[this.command.selectedIndex];
			BizRCHub.JobMarkAsVacation(entity.HRTimeSheetPK).then(res=>{
				if(res.data.OK)
				{
					let item = res.data.Result;
					Object.assign(entity, item);
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		MarkAsWork() {
			this.$showLoading();
			let entity = this.listArray[this.command.selectedIndex];
			BizRCHub.JobMarkAsWork(entity.HRTimeSheetPK).then(res=>{
				if(res.data.OK)
				{
					let item = res.data.Result;
					Object.assign(entity, item);
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		MarkAsAbsent() {
			this.$showLoading();
			let entity = this.listArray[this.command.selectedIndex];
			BizRCHub.JobMarkAsAbsent(entity.HRTimeSheetPK).then(res=>{
				if(res.data.OK)
				{
					let item = res.data.Result;
					Object.assign(entity, item);
				}
				else
				{
					this.$alert(res.data.Error, 'Error');
					console.log(res.data.Error);
				}
				this.$closeLoading();
			}).catch(err=>{
				this.showNetworkError();
				console.log(err);
				this.$closeLoading();
			});
		},
		MarkAsTerminate() {
			this.$prompt('Please input reason').then(({ value, action })=>{
				this.$showLoading();
				let entity = this.listArray[this.command.selectedIndex];
				BizRCHub.JobMarkAsTerminate(entity.HRTimeSheetPK, value).then(res=>{
					if(res.data.OK)
					{
						let item = res.data.Result;
						Object.assign(entity, item);
					}
					else
					{
						this.$alert(res.data.Error, 'Error');
						console.log(res.data.Error);
					}
					this.$closeLoading();
				}).catch(err=>{
					this.showNetworkError();
					console.log(err);
					this.$closeLoading();
				});
			}).catch(err=>{});
		}
	},
	mounted()
	{
		this.manager = Utils.getUserSettings();
		
		if(this.$store.state.jobTimeSheetList.length > 0)
		{
			this.listArray = this.$store.state.jobTimeSheetList;
		}
		else
		{
			this.getList(this.manager.SearchJobNumber, this.manager.SearchDateTime);
		}	
	}
}
</script>

<style lang="scss" scoped>
	.page-header {
		background-color: #a9362d;
		position: fixed;
		width: 100%;
		top: 0;
		left: 0;
		z-index: 100;
	}
	.mint-header {
		background-color: #a9362d;
	}
	.page-body{
		margin-bottom: 38px;
		margin-top: 40px;
	}
	.menu {
		width: 260px;
		height: 100%;
		background-color: #c45050;
	}
	ul{
    	list-style: none;
		color: #fff;
		padding: 20px;
	}
	ul li {
		padding: 20px 0;
		border-bottom: 1px solid rgba(255,255,255,0.2);
		transition: all ease 0.3s;
	}
	ul li:hover {
		border-bottom-color: #fff;
	}
	.menu svg {
		margin-right: 10px;
	}
	.mint-tabbar {
		position: static !important;
	}
	.mint-tabbar > .mint-tab-item {
		background-color: #a9362d;
		color: #fff;
	}
	.tabbar-wrapper {
		background-color: #a9362d;
		position: fixed;
		bottom: 0;
		left: 0;
		width: 100%;
	}
	.dateTime,
	.postInOut {
		display: inline-block;
		margin-bottom: 6px;
		color: #8e8e93;
	}
	.checkInOut {
		display: inline-block;
		color: #8e8e93;
	}
	.checkInOut .svg-inline--fa,
	.postInOut .svg-inline--fa {
		margin-right: 10px;
	}
	.dateTime {
		font-weight: bolder;
	}
	.page-body .functions {
		color:#c45050;
		margin-left: 8px;
		font-size: 1.1em;
	}
	.page-body div {
		border-bottom: 1px solid #ddd;
	}
	.mint-tabbar > .mint-tab-item.is-selected {
		background-color: #a9362d;
		color: #fff;
	}
	.wrapper {
		padding: 10px 5px;
	}
	.functions svg {
		width: 1em;
	}	
	.mint-popup.search {
		width: 80%;
	}
	.button-group .mint-button--large {
		display: inline-block;		
		width: 50%;
	}
	.mint-button--primary {
		background-color: #a9362d;
	}
	.mint-cell, .mint-cell-wrapper {
		background-image: none;
	}
	.search {
		width: 80%;
		padding: 5px 10px 10px;
	}
</style>
<style>
	.picker-btn-sure {
		color: #a9362d !important ;
	} 
	.picker-btn-cancel {
		color: #333 !important ;
	}   
	.mint-datetime-confirm,
	.mint-msgbox-confirm {
		color: #a9362d;
	}
	.mint-datetime-cancel {
		color: #333;
	}
	.job-list .mint-cell, .job-list .mint-cell-wrapper {
		background-image: none;
	}
</style>
